.header-login {
  background-color: #FFFFFF;
  width: 100%;
  height: 70px;
  color: #8f90a0;
  box-shadow: 0px 10px 20px rgba(31, 32, 65, 0.05);
  margin-bottom: 40px;
  margin-top: 40px;
}

.header-login_body {
  display: flex;
  margin-top: auto;
}

.header_logo {
  margin-top: 15px;
  margin-bottom: 15px;
}

.nav-login {
  display: flex;
  width: 447px;
  height: 30px;
  margin-left: 466.31px;
  margin-top: 26px;
  margin-bottom: 26px;
  padding-left: 0px;
  padding-right: 0px;
  font-size: 14px;
}

.nav-item {
  list-style-type: none;
  padding-right: 0px;
  margin-right: 20px;
}

.nav-item-text {
  padding-right: 0px;
}

.nav-link-about {
  color: rgba(31, 32, 65, 0.75);
  font-family: "MontserratBold";
}

.nav-item-arrow {
  width: 74px;
  display: flex;
}

.nav-arrow {
  font-family: "MaterialIcons";
  font-style: normal;
  font-weight: normal;
  font-size: 24px;
  line-height: 20px;
  margin-left: 0px;
  margin-right: 0px;
}

.vl {
  border-left: 1px solid rgba(31, 32, 65, 0.1);
  height: 30px;
  margin-top: 20px;
  margin-bottom: 20px;
  margin-left: 20px;
}

.nav-login-name {
  white-space: nowrap;
  height: 18px;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 17px;
  font-family: "MontserratRegular";
  color: rgba(31, 32, 65, 0.5);
  padding-top: 26px;
  padding-bottom: 26px;
  margin-left: 20px;
}