.header {
  background-color: #FFFFFF;
  width: 100%;
  height: 70px;
  color: #8f90a0;
  box-shadow: 0px 10px 20px rgba(31, 32, 65, 0.05);
}

.header_body {
  display: flex;
  justify-content: space-between;
  margin-top: auto;
}

.header_logo {
  margin-top: 15px;
  margin-bottom: 15px;
}

.nav {
  display: flex;
  width: 447px;
  height: 21px;
  margin-left: 264.31px;
  margin-top: 26px;
  margin-bottom: 26px;
  padding-left: 0px;
  padding-right: 0px;
  font-size: 14px;
}

.nav-item {
  list-style-type: none;
  padding-right: 0px;
  margin-right: 20px;
}

.nav-item-text {
  padding-right: 0px;
}

.nav-link {
  text-decoration: none;
  color: rgba(31, 32, 65, 0.5);
  font-family: "MontserratRegular";
  font-style: normal;
}

.nav-link-about {
  color: rgba(31, 32, 65, 0.75);
  font-family: "MontserratBold";
}

.nav-item-arrow {
  width: 74px;
  display: flex;
}

.nav-arrow {
  font-family: "MaterialIcons";
  font-style: normal;
  font-weight: normal;
  font-size: 24px;
  line-height: 20px;
  margin-left: 0px;
  margin-right: 0px;
}

.headerbtn {
  border-radius: 100rem;
  margin-top: 18px;
  margin-bottom: 18px;
  border: solid 2px transparent;
  background-image: linear-gradient(180deg, #BC9CFF 0%, #8BA4F9 100%);
  background-clip: content-box, border-box;
  box-shadow: 2px 1000px 1px #FFFFFF inset;
  font-family: inherit;
  font-family: "MontserratBold";
  font-size: 12px;
  line-height: 15px;
  text-transform: uppercase;
}

.headerbtn-log-in {
  width: 87px;
  height: 34px;
  color: #BC9CFF;
  text-align: center;
}

.headerbtn-sign-up {
  width: 196px;
  height: 34px;
  background: linear-gradient(180deg, #BC9CFF 0%, #8BA4F9 100%);
  box-shadow: none;
  color: #FFFFFF;
  position: relative;
  border: none;
}