.footer {
  position: absolute;
  display: flex;
  flex-direction: column;
  background-color: #FFFFFF;
  height: 443px;
  width: 100%;
  box-shadow: 0px 10px 20px rgba(31, 32, 65, 0.05);
  font-family: "MontserratRegular";
}

.footer_section-1 {
  height: 372px;
}

.footer_section-1-body {
  position: relative;
  display: flex;
  flex-flow: nowrap;
  flex-direction: row;
  flex-wrap: nowrap;
  align-content: stretch;
  align-items: stretch;
  justify-content: start;
  padding-top: 100px;
}

.footer-item-logo {
  display: flex;
  flex-direction: column;
  margin-right: 40px;
}

.footer_logo {
  margin-bottom: -8px;
}

.footer_logo_text {
  width: 260px;
  height: 72px;
  padding-top: 20px;
  font-size: 14px;
  line-height: 24px;
  color: rgba(31, 32, 65, 0.75);
}

.footer-item-nav {
  width: 143px;
  margin-right: 57px;
  padding-left: 0px;
}

.footer-item-about {
  width: 108px;
  margin-right: 92px;
  padding-left: 0px;
}

.footer-item-support {
  width: 149px;
  margin-right: 51px;
  padding-left: 0px;
}

.footer-item-subscribe {
  width: 260px;
  display: flex;
  flex-direction: column;
}

.footer-subscribe-bold {
  text-transform: uppercase;
  font-size: 12px;
  margin-top: 0px;
  margin-bottom: 20px;
  padding-top: 5px;
  padding-bottom: 0px;
}

.footer-subscribe-text {
  width: 260px;
  height: 84px;
  font-size: 14px;
  line-height: 24px;
  padding-top: 0px;
  margin-bottom: 20px;
}

.footer-list {
  list-style-type: none;
  margin: 0px;
  padding-left: 0px;
  padding-top: 5px;
}

.footer-list-item {
  margin-bottom: 20px;
}

.footer-list-link:active,
.footer-list-link:hover,
.footer-list-link {
  text-decoration: none;
  color: rgba(24, 24, 32, 0.5);
  font-size: 14px;
  line-height: 17px;
}

.footer-list-link-bold {
  color: rgba(31, 32, 65, 0.75);
  font-family: "MontserratBold";
  font-size: 12px;
  text-transform: uppercase;
  padding-bottom: 2px;
}

.footer-subscribe-text {
  width: 260px;
  height: 48px;
  font-family: "MontserratRegular";
  font-style: normal;
  font-weight: normal;
  color: rgba(31, 32, 65, 0.75);
  font-size: 14px;
  line-height: 24px;
  padding-top: 0px;
}

.footer_section-2 {
  position: relative;
  display: flex;
  flex-direction: row;
  justify-content: start;
  align-items: center;
  height: 70px;
  border: 1px solid rgba(31, 32, 65, 0.05);
  border-left: none;
  border-bottom: none;
  border-right: none;
  width: 100%;
}

.footer_section-2-text {
  padding-left: 140px;
  padding-right: 676px;
  font-family: "MontserratRegular";
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 17px;
  color: rgba(31, 32, 65, 0.75);
}

.social {
  display: flex;
  flex-direction: row;
}

.social-svg {
  width: 0px;
  height: 0px;
}

.gradient-icon {
  font-family: "Font Awesome 5 Brands";
  font-size: 24px;
  padding-right: 20px;
  text-align: right;
}

path {
  fill: url(#Gradient-1);
}